:root {
  --column-number: 1;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font: 500 12px Roboto, Arial, sans-serif;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  display: flex;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(var(--column-number), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-number: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-number: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-number: 4;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font: 500 12px / 18px Roboto, Arial, sans-serif;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__image {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__text {
  color: #616070;
  margin: 0;
  font: 400 10px / 14px Roboto, Arial, sans-serif;
}

.card__text--money {
  font-size: 12px;
  line-height: 18px;
}

.card__text--value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__text--product-code {
  margin-bottom: 16px;
}

.card__text--reviews {
  color: #060b35;
}

.card__stars {
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__button-buy {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding-block: 11px;
  font: 700 14px / 16px Roboto, Arial, sans-serif;
  text-decoration: none;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Regular.ca197847.ttf");
  font-weight: 400;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.91322885.ttf");
  font-weight: 500;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Bold.fdb9b54a.ttf");
  font-weight: 700;
}

.body {
  margin: 0;
}
